html,
body {
  padding: 0;
  margin: 0;
}

.approot {
  display: grid;
  grid-gap: 0;
  grid-template-columns: 1fr 4fr;
  padding: 0;
  margin: 0;
  min-height: 100vh;
}

.approot section {
  padding: 40px;
  margin: 0;
}

.approot .content {
  grid-column-start: 2;
  grid-row-start: 1;
}

.approot .contact {
  grid-column-start: 1;
  grid-row-start: 1;
  background-color: #333333;
  color: #ffffff;
}
